  width: 100vw;
  font-size: 3vw;
  background-color: #f9f9f9;
  .listContent {
    width: 98vw;
    margin: 0 auto;
    // height: 66.5vh;
    display: flex;
    overflow-y: hidden;
    .contentRight,
    .contentLeft {
      width: 50%;
      position: relative;

      .listContentItem {
        width: 100%;
        height: 34.65vh;
        margin-top: 1.5vw;
        float: left;
        border-radius: 2vw;
        padding: 1vw;
        box-sizing: border-box;
        background-color: #fff;
        color: #434343;
        font-size: 3.4vw;
        font-weight: 500;
        .listContentTop {
          width: 100%;
          height: 72.85%;
          position: relative;
          .TopImg {
            width: 100%;
            height: 100%;
          }
          .TopFont {
            padding: 1vw;
            color: rgba(230, 230, 230, 0.938);
            background-color: rgba(82, 81, 81, 0.603);
            border-radius: 1vw;
            position: absolute;
            top: 0;
            right: 0;
          }
          .TopText {
            width: 100%;
            display: flex;
            position: absolute;
            bottom: 3vw;
            justify-content: left;
            align-items: center;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
            img {
              width: 3.5vw;
              height: 3.5vw;
              margin-left: 2vw;
            }
            div {
              font-size: 3.2vw;
              color: white;
              margin-left: 3vw;
            }
          }
        }
        .listContentBom {
          // list-style: none;
          display: flex;
          flex-direction: column;
          justify-content: space-between;
          margin-top: 1.43%;
          padding: 1vw;

          .fabulous {
            height: 3vh;
            line-height: 3vh;
            display: flex;
            justify-content: space-between;
            margin-bottom: 1vw;
            div:nth-child(2) {
              display: flex;
              img {
                width: 4vw;
                height: 4vw;
                margin-top: 0.4vw;
              }
            }
          }
        }
      }
      .AllText {
        margin-top: 2.5vw;
        width: 100%;
        text-align: center;
        height: 5.8vh;
        color: white;
        line-height: 5.8vh;
        border-radius: 1vw;
        background-color: #3fc18d;
        font-size: 4vw;
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    .contentRight {
      display: inline-block;
    }
  }
}
.van-list {
  width: 100%;
}
