






























































































































































































































































































































.caseIndex {
  width: 100%;
  max-width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
  display: flex;

  .optionBtn {
    font-size: 1.04vh;
    font-weight: 400;
    color: #535353;
    cursor: pointer;
    position: absolute;
    top: 1.04vh;
    left: 1.85vw;
    z-index: 9;
  }
  .caseContainerBox {
    width: 100%;
    flex: 1;
  }
  .optionBox {
    position: absolute;
    top: -0.52vh;
    right: 0;
    width: calc(100% - 2.08vw);
    height: 100%;
    z-index: 9;
    border-left: 1px solid #8fbed4;
    background: #fff;
    padding: 0 1.04vw;
    transition: all 0.4s;
    box-shadow: 2px -2px 5px rgba(0, 0, 0, 0.5);
    .optionHead {
      margin-top: 0.52vh;
      height: 5.92vh;
      line-height: 5.92vh;
      font-size: 1.3vh;
      font-weight: 400;
      color: #535353;
      text-align: left;
    }
    .clearBox {
      width: 100%;
      text-align: left;
      .clearBtn {
        display: inline-block;
        opacity: 0.85;
        font-size: 1.67vh;
        color: #218bbd;
        cursor: pointer;
      }
    }
    .optionTitle {
      height: 3.43vh;
      font-size: 2.41vh;
      text-align: left;
      color: #535353;
      line-height: 3.43vh;
      margin-top: 1.65vh;
    }
    .activityType {
      height: 22%;
      overflow-y: auto;
      &::-webkit-scrollbar {
        display: none;
      }
    }
    .options {
      float: left;
      margin-bottom: 12px;
      .optionItem {
        display: inline-block;
        width: 14.62vw;
        height: 3.96vh;
        background: #f8fbfb;
        border-radius: 2px;
        line-height: 3.96vh;
        text-align: center;
        font-size: 1.67vh;
        color: #686868;
        margin-left: 0.78vw;
        cursor: pointer;
        &:hover {
          background: #d3f4ff;
        }
      }
      .optionItem.active {
        background: #d3f4ff;
      }
    }
    .optionSaveBox {
      position: absolute;
      bottom: 2.31vh;
      left: 0;
      width: 100%;
      height: 3.59vh;
      line-height: 3.59vh;
      text-align: center;
      color: #218bbd;
      font-size: 1.3vh;
      .optionSaveBtn {
        width: 14.73vw;
        background: #f8fbfb;
        margin: 0 0.36vw;
        display: inline-block;
        cursor: pointer;
      }
    }
  }
}
.el-date-editor.el-input,
.el-date-editor.el-input__inner {
  width: 100% !important;
}

.el-checkbox {
  width: calc(50% - 30px);
}
